import React, { Component } from 'react';
import { Card, Button } from 'react-bootstrap';

class Event extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false
    }
  }
  convertDateEvent = (date) => {
    return new Date(date).toLocaleDateString()
  }
  convertTimeEvent = (date) => {
    return new Date(date).toLocaleTimeString().slice(-2)
  }
  handleShowDetails = (prevState) => {
    this.setState({
      visible: !prevState.visible
    })
  }

  render() {
    return (
      <div className='event' >
        <Card>
          <Card.Header className='summary'>
            {this.props.event.summary}
            <Button variant="primary" className='details-button btn-sm'
              onClick={() => this.handleShowDetails(this.state)} >{this.state.visible ? 'Hide details' : 'Show details'}
            </Button>
          </Card.Header>
          <Card.Body>
            {this.state.visible ? (
              <Card.Text className='eventDescription'>{this.props.event.description}</Card.Text>
            ) : null}
            <Card.Title className='dateTime'>
              {this.convertDateEvent(this.props.event.start.dateTime)}
              {this.convertTimeEvent(this.props.event.start.dateTime)}
            </Card.Title>
          </Card.Body>
        </Card>
      </div>
    )


  }
}
export default Event;